.navbar-toggle {
    background: none;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    border-radius: 0;
    margin: 28px 6px 10px;
    padding: 12px 14px;
    border: 2px solid black;
    color: #000;
}

.btn-navbar:hover {
    background-color: transparent;
    background-image: none;
    border-radius: 0;
    border: 0 !important;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
    padding-top: 23px;
}

.btn.btn-primary {
    color: #fff;
}

.btn.btn-primary:focus {
    color: #fff;
}

.open>.btn-primary.dropdown-toggle:focus,
.open>.btn-primary.dropdown-toggle:hover {
    color: #fff;
}

.bar {
    fill: steelblue;
}

.axis path {
    display: none;
}

.robotoh1 {
    font-size: 112px;
    font-weight: lighter;
}

.robotoh2 {
    font-size: 67px;
    font-weight: lighter;
}

.robotoh3 {
    font-size: 40px;
    font-weight: lighter;
}

.robotoh4 {
    font-size: 37px;
    font-weight: lighter;
}

.robotoh5 {
    font-size: 16px;
    font-weight: lighter;
}

.text_red_glaze {
    color: rgb(255, 186, 186);
}

.text_blue_glaze {
    color: rgb(204, 210, 252);
}

@media screen and (max-width: 768px) {
    .poll-response .card {
        margin-top: 50px;
    }
    .stats_container {
        .robotoh4 {
            font-size: 32px;
            text-align: center;
        }
        .robotoh5 {
            text-align: center;
        }
    }
    .container {
        width: 80%;
    }
}

.robotoHeader {
    font-weight: 700;
    font-size: 25x;
}


